import React from 'react';
import styled from 'styled-components';
import QuestionCell from './QuestionCell';



const StyledDiv = styled.div`
background: #FFFFFF;
border: 1px solid #E0E0E0;
box-sizing: border-box;
border-radius: 12px;
margin-top: 16px;
`

const StyledDiv2 = styled.div`
padding-top: 15px;
padding-bottom: 15px;
padding-left: 15px;
padding-right: 15px;
font-family: Raleway;
`

const QuestionTable = (props) => {
    const {cells} = props;
    return (
        <StyledDiv>
           {cells ? 
           cells.map(({questionText, answerText, attributeText}) => (
            <QuestionCell 
            questionText={questionText} attributeText={attributeText} answerText={answerText}/>
        ))
             : <StyledDiv2>
                 No Results Right Now
               </StyledDiv2>
           }
        </StyledDiv>
    )
}

export default QuestionTable;