import React from 'react';
import styled from 'styled-components';
import QuestionTable from './lib/QuestionTable/QuestionTable';
import QuestionCell from './lib/QuestionTable/QuestionCell';
import SearchBar from './lib/SearchBar/SearchBar';
import SearchByButton from './lib/SearchBar/SearchByButton'
import NavBar from './lib/NavBar/NavBar';

const cells = [
  {questionText: "question", answerText: "answer", attributeText: "attribute"},
  {questionText: "question", answerText: "answer", attributeText: "attribute"},
  {questionText: "question", answerText: "answer", attributeText: "attribute"},
  {questionText: "question", answerText: "answer", attributeText: "attribute"},
  {questionText: "question", answerText: "answer", attributeText: "attribute"}
]

const StyledDiv = styled.div`
padding-top: 18px;
padding-left: 60px;
padding-right: 60px;
padding-bottom: 100px;
`

const executeRequest = async () => {
  const response = await fetch("http://jservice.io/api/random");
  const myJson = await response.json();
  const cells = myJson.map(currentObj => (
    {questionText:currentObj.question, answerText:"ANSWER: " + currentObj.answer, 
    attributeText:"\"" + currentObj.category.title + "\"" + " Â· " + currentObj.value + " points"}
  ))
  return cells;
}

function App() {
  const [cells, setCells] = React.useState(null);
  return (
    <div className="App">
      <StyledDiv>
        <NavBar/>
          <SearchBar onSearch={async () => {
          const cells = await executeRequest()
          setCells(cells);
          }
            }/>
          <QuestionTable cells={cells}/>
      </StyledDiv>
    </div>
  );
}

export default App;
